.works {
  padding-top: 50px;
  padding-bottom: 100px;
  h2 {
    margin-bottom: 45px;
    text-align: center; }
  p {
    line-height: 25px;
    font-size: 16px;
    color: #595959;
    margin-bottom: 70px;
    padding: 0 150px;
    text-align: center; }
  &__block {
    cursor: pointer;
    text-decoration: none;
    &:hover {
      .works__block__image {
        &:before {
          opacity: 1; } }
      .btn {
        opacity: 1; } }
    &__image {
      width: 100%;
      position: relative;
      &:before {
        transition: all 0.3s;
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.7) 100%);
        opacity: 0; }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover; } }
    &__text {
      h3 {
        font-size: 24px;
        text-align: center;
        margin-top: 45px;
        margin-bottom: 40px;
        font-family: $noto;
        color: black;
        height: 50px;
        padding: 0 60px; }
      span {
        font-size: 14px;
        text-align: center;
        display: block;
        color: black;
        font-weight: bold;
        opacity: 0.6; } } } }
