.home-services__image:after {
  content: '';
  width: 100px;
  height: 80%;
  background: #2E3549;
  position: absolute;
  right: -45px;
  top: 50%;
  transform: translateY(-50%);
}

.home-services__block h2 {
  margin-bottom: 45px;
  color: white;
}

.home-services__block p {
  font-size: 16px;
  line-height: 25px;
