  flex-wrap: wrap;
  justify-content: space-between;
}

.home-services__block__links a {
  text-decoration: none;
  color: white;
  display: inline-flex;
  align-items: center;
  padding-right: 20px;
  margin-bottom: 40px;
  position: relative;
}

.home-services__block__links a:before {
  content: '';
  width: 60px;
  height: 60px;
  background: white;
  border-radius: 200px;
  position: absolute;
  left: 0;
  top: 0;
