.home-services__block__links a i {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  font-size: 27px;
  margin-right: 20px;
  position: relative;
  z-index: 1;
}

.home-services__block__links a span {
